import React, { useState } from 'react'
import s from './Paginator.module.css'
let Paginator = (props,{portionSize = 10}) =>{
    let pagesCount = Math.ceil(props.totalItemsCount / props.pageSize)
        let pages = [

        ]
        for (let i = 1;i<= pagesCount; i++){
            pages.push(i)
        }
    let portionCount = Math.ceil(pagesCount / portionSize)
    let [portionNumber, setPortionNumber]  = useState(1)
    let leftPortionPageNumber = (portionNumber - 1) * portionSize;
    let rightPortionPageNumber = portionNumber * portionSize
       
    return(
        <div className={s.paginator}>
            {portionNumber >1 &&
                <button onClick={() =>{setPortionNumber(portionNumber -1)}}>Prew</button>
            }
            {pages
            .filter(p=> p>= leftPortionPageNumber && p <=rightPortionPageNumber)
            .map(p =>{
                        return <span className={props.currentPage === p && s.selectedPage} onClick={(e)=>{props.onPageChange(p);}}>{p}</span>
                    })}
            {portionCount > portionNumber &&
                <button onClick={() =>{setPortionNumber(portionNumber + 1)}}>Next</button>
            }        
        </div>
    )
}
export default Paginator